.personal_container {
  width: 100%;
  background: #f4f4f4;
  height: 100%;
  padding-top: 1rem;
}
.personal_container .ant-menu-inline,
.personal_container .ant-menu-vertical,
.personal_container .ant-menu-vertical-left {
  border-right: none;
}
.personal_container .ant-layout {
  background: #f4f4f4;
  height: 100%;
}
.personal_container .ant-layout .ant-layout-sider {
  background-color: #f4f4f4;
  margin-right: 0.5rem;
}
.personal_container .ant-layout .ant-layout-sider .personal_info {
  text-align: center;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #fff;
  border: 1px solid #ece5e5;
  color: #000;
}
.personal_container .ant-layout .ant-layout-sider .personal_menu {
  text-align: center;
  border: 1px solid #ece5e5;
  min-height: 22rem;
  background-color: #fff;
}
.personal_container .ant-layout .ant-layout-sider .personal_menu ul {
  width: 6rem;
  margin: auto;
}
.personal_container .ant-layout .ant-layout-sider .personal_menu ul .ant-menu-item-selected {
  background: none;
  border-bottom: 2px solid #FCB316;
}
.personal_container .ant-layout .ant-layout-header {
  background-color: #fff;
  height: 40px;
  padding: 0 10px;
  line-height: 40px;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
}
.personal_container .ant-layout .ant-table,
.personal_container .ant-layout .ant-divider {
  font-size: 0.86rem;
}
.personal_container .ant-layout .ant-divider,
.personal_container .ant-layout .ant-divider-vertical {
  margin: 0 0.5rem;
}
.personal_container .ant-layout .ant-table-tbody {
  background-color: #fff;
}
.personal_container .personal_main {
  margin-bottom: 0;
}
.personal_container .personal_main .spacemanage-container {
  background: #fff;
}
.personal_container .personal_main .spacemanage-container .spacemanage-title {
  background: #fff;
}
.personal_container .personal_main .spacemanage-container .spacemanage-title .spacemanage-title_content {
  text-align: left;
  height: 4rem;
  font-size: 1.5rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #333333;
  line-height: 4rem;
  border-bottom: 1px solid #EDEDED;
}
.personal_container .personal_main .spacemanage-container .spacemanage-title .spacemanage-title_content .spacemanage-title_info {
  border-bottom: 3px solid #434343;
  display: block;
  width: 10rem;
  height: 4rem;
  text-align: center;
}
.personal_container .personal_main .spacemanage-container .spacemanage-sort_btn button {
  border-radius: 0;
}
.personal_container .personal_main .spacemanage-container .spacemanage-sort_btn button.selected_btn {
  border-color: #026dff;
  color: #026dff;
}
.personal_container .personal_main .spacemanage-container .spacemanage-sort_btn :last-child {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.personal_container .personal_main .spacemanage-container .spacemanage-sort_btn :first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.personal_container .personal_main .customTabs .ant-tabs-bar {
  text-align: left;
}
.personal_container .personal_main .customTabs .ant-tabs-bar .ant-tabs-tab {
  color: #999999;
  font-size: 1.5rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  padding: 14px 16px;
}
.personal_container .personal_main .customTabs .ant-tabs-bar .ant-tabs-tab:hover {
  color: #026dff;
}
.personal_container .personal_main .customTabs .ant-tabs-bar .ant-tabs-tab-active {
  font-size: 1.5rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #333333;
}
.personal_container .personal_main .customTabs .ant-tabs-bar .ant-tabs-ink-bar {
  background-color: #333333;
  height: 3px;
}
.personal_container .personal_main .personal-data {
  background: #fff;
}
.personal_container .personal_main .personal-data .personal-data-div {
  padding-top: 5rem;
  padding-bottom: 5rem;
}
.personal_container .personal_main .personal-data .personal-data-div .ant-row {
  height: 4rem;
}
.personal_container .personal_main .personal-data .personal-data-div .ant-row .personal-data-div_item_label {
  text-align: right;
  font-size: 1rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #0d0d0d;
}
.personal_container .personal_main .personal-data .personal-data-div .ant-row .personal-data-div_item_value {
  text-align: left;
  font-size: 1rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #0d0d0d;
}
.personal_container .personal_main .personal-data .personal-data-div button {
  background: #4993F0;
  font-size: 1rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #ffffff;
  height: 2rem;
  width: 10rem;
}
.personal_container .personal_main .personal-data .personal-data-div button:hover {
  background: #026dff;
}
.personal_container .personal_main .teacher_table_data .ant-table-tbody .td_filename {
  vertical-align: top;
  padding: 0 1rem 0 0.5rem;
}
.personal_container .personal_main .teacher_table_data .ant-table-tbody .td_filename a.edit {
  color: #1890ff;
  position: absolute;
  top: 0;
  right: -1rem;
}
.personal_container .personal_main .teacher_table_data .ant-table-tbody .td_filename a.filename_a {
  color: #1890ff;
}
.personal_container .personal_main .teacher_table_data .ant-table-tbody .td_filename a.filename_a:hover {
  color: red;
}
.personal_container .personal_main .teacher_table_data .ant-table-tbody .td_filename p {
  margin: 0.3rem auto;
}
.personal_container .personal_main .teacher_table_data .ant-table-tbody .td_filename a {
  color: #615c5c;
}
.personal_container .personal_main .teacher_table_data .ant-table-tbody .td_filename a:hover {
  color: #026dff;
}
.personal_container .personal_main .teacher_table_data .ant-table-tbody tr td {
  padding: 8px;
}
.personal_container .personal_main .resourcemangener-container {
  background: #fff;
}
.personal_container .personal_main .resourcemangener-container .addCourseBtn {
  background: #4993f0;
  color: #fff;
  margin-left: 1rem;
  border-radius: 5px;
  width: 5rem;
}
.personal_container .personal_main .resourcemangener-container .addCourseBtn:hover {
  background: #026dff;
}
.personal_container .personal_main .notesPage-body .notesPage-title {
  background: #fff;
}
.personal_container .personal_main .notesPage-body .notesPage-title .notesPage-title_content {
  text-align: left;
  height: 4rem;
  font-size: 1.5rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #333333;
  line-height: 4rem;
  border-bottom: 1px solid #EDEDED;
}
.personal_container .personal_main .notesPage-body .notesPage-title .notesPage-title_content .notesPage-title_info {
  border-bottom: 3px solid #434343;
  display: block;
  width: 10rem;
  height: 4rem;
  text-align: center;
}
.personal_container .ant-spin-container {
  text-align: center;
}
.personal_container .ant-spin-container .ant-table-pagination {
  float: none;
}
.pingtai-choice .ant-modal-close-icon {
  color: #fff;
}
.pingtai-choice .ant-modal-header {
  background: #028BFF;
  text-align: center;
}
.pingtai-choice .ant-modal-header .ant-modal-title {
  color: #fff;
}
.pingtai-choice .ant-modal-body .pingtai-choice_button {
  text-align: center;
  margin-top: 2rem;
}
.home-boyd-list-div-list {
  width: 100%;
  margin: 0 auto;
  background-color: #fff;
}
.home-boyd-list-div-list .home-boyd-list-div-list-style {
  text-align: center;
}
.home-boyd-list-div-list .home-boyd-list-div-list-style .home-boyd-list-div-list-style-dataImg {
  height: 26rem;
  text-align: center;
  padding-top: 6rem;
  display: inline-block;
  width: 32rem;
  display: -webkit-inline-box;
}
.home-boyd-list-div-list .home-boyd-list-div-list-style .home-boyd-list-div-list-style-dataImg .dataImg {
  margin-top: 2rem;
}
.home-boyd-list-div-list .home-boyd-list-div-list-style .home-boyd-list-div-list-style-dataImg .dataImg .dataImg-span {
  font-size: 1.1rem;
  color: #7e7d7c;
  margin-bottom: 1.1rem;
}
.home-boyd-list-div-list .home-boyd-list-div-list-style .home-boyd-list-div-list-style-dataImg .dataImg .dataImg-bnts {
  background: #4993f0;
  width: 130px;
  height: 34px;
  line-height: 34px;
  border: none;
}
.preview_modal {
  top: 70px;
}
.preview_modal .ant-modal-content {
  height: inherit;
}
.preview_modal .ant-modal-content .ant-modal-body {
  padding: 0;
  height: calc(100% - 55px);
}
.ant-modal-header {
  background: #028bff;
  text-align: center;
}
.ant-modal-header .ant-modal-title {
  color: #fff;
  font-size: 1.13rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #ffffff;
}
.ant-modal-close .ant-modal-close-x {
  color: #fff;
}
.previewvideo-modal-container .ant-modal-header,
.previewvideo-modal-container .ant-modal-body {
  padding: 0;
  background-color: #3e3a3a;
  border: none;
}
.previewvideo-modal-container .ant-modal-close-x {
  width: 20px;
  height: 20px;
  line-height: 20px;
}
